<template>
    <home-container title="Alert Settings" default-href="/home/account/notification" :with-header='true'>
        <ion-label position='stacked'>Currency</ion-label>
        <ion-item class='border ion-margin-bottom' lines='none'>
            <ion-input readonly value='Bitcoin'></ion-input>
        </ion-item>
        <ion-label position='stacked'>Alert me</ion-label>
        <ion-item  class="border ion-margin-bottom" lines="none">
            <ion-select value='Once' interface='action-sheet'  placeholder="Select Plan" :interface-options="{header: 'Alert Me'}">
                <ion-select-option v-for="(i,index) in ['Once','Everytime']"  :key='index'>{{i}}</ion-select-option>
            </ion-select>
        </ion-item>
        <ion-label position='stacked'>When the price of 1 BTC is</ion-label>
        <ion-item  class="border ion-margin-bottom" lines="none">
            <ion-select value='More than' interface='action-sheet'  placeholder="Select Plan" :interface-options="{header: 'Price of 1 BTC'}">
                <ion-select-option v-for="(i,index) in ['More than','Less than']"  :key='index'>{{i}}</ion-select-option>
            </ion-select>
        </ion-item>
        <ion-label position='stacked'>Amount</ion-label>
        <ion-item class='border ion-margin-bottom' lines='none'>
            <ion-label style='padding-right:10px;border-right:1px solid #e0e5eb'>USD</ion-label>
            <ion-input type='number' placeholder="Enter amount here"></ion-input>
        </ion-item>
        <ion-button strong @click.prevent="openToast"  class='ion-margin-horizontal my-5' expand="block">
            <ion-label>Create Alert</ion-label>
        </ion-button>
    </home-container>
    
</template>
<script>
import { defineComponent } from "vue";
import { toastController } from "@ionic/vue";
import { useRouter } from "vue-router";

    export default defineComponent({
        components: { },
        data() {
            return {
            }
        },
        setup() {
            const router = useRouter();
            return {
                router
            };
        },
        methods: {
            async openToast() {
                const toast = await toastController
                    .create({
                    message: 'Operation Successfull!',
                    duration: 2000,
                    color:'dark'
                    })
                return toast.present();
            }
        }
});

</script>
